"use client";
import { useContext, useEffect } from "react";
import { UserContext } from "@/context/user.context";
import { useRouter } from "next/navigation";
import { useSearchParams } from "next/navigation"; // Importar useSearchParams

export default function AuthCallback() {
  const router = useRouter();
  const { loginWithGoogle } = useContext(UserContext);
  const searchParams = useSearchParams(); // Obtener los parámetros de la URL

  useEffect(() => {
    const code = searchParams.get("code"); // Obtener el valor de "code"

    if (code) {
      const googleToken = code;

      loginWithGoogle(googleToken)
        .then(() => {
          router.push("/");
        })
        .catch((error) => {
          console.error("Error al iniciar sesión con Google:", error);
        });
    }
  }, [searchParams, loginWithGoogle, router]);

  return <div className="m-auto">Cargando...</div>;
}
